export default {
	data() {
		return {
			info:{},
			rules:[]
		};
	},
	onNavigationBarButtonTap(e) {
		uni.navigateTo({
			url: '../record/record'
		});
	},
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	onLoad() {
		this.$Fetch({
			url:"Points/getDataById",
			success:res=>{
				this.info=res;
				this.rules=res.lists
			}
		})
	}
};
